/**
 * For handling Remembered Posts widget (a8c-only).
 *
 */

( function ( $ ) {
	if ( $( '#sidebar .widget_p2020-filter-widget' ).length > 0 ) {
		$( '#remembered-posts' ).insertAfter(
			'#sidebar .widget_p2020-filter-widget'
		);
	} else {
		$( '#remembered-posts' ).insertBefore(
			'#sidebar .widget:first-of-type'
		);
	}
} )( jQuery );
