( function ( $ ) {
	// Also defined in css/src/global/_variables.scss
	const breakpoint = '876px';

	const originalSidebarContainerId = 'main-wrapper';
	const mobileMenuClass = 'is-mobile-menu';
	const modalWrapperClass = 'p2020-modal-wrapper';
	const sidebarId = 'sidebar';
	const mobileMenuId = 'mobile-menu';
	const adminbarId = 'wpadminbar';
	const adminbarModifierClass = 'is-below-modal';

	function setupMobileMenu( $sidebar ) {
		$sidebar.addClass( mobileMenuClass );
		$sidebar.attr( 'role', 'dialog' );
		$sidebar.attr( 'aria-modal', 'true' );

		$sidebar.wrap(
			`<div id="${ mobileMenuId }" class="${ modalWrapperClass }" data-micromodal-close aria-hidden="true" />`
		);

		$( `.${ modalWrapperClass }` ).appendTo( $( '#page' ) );

		$( `#${ adminbarId }` ).addClass( adminbarModifierClass );
	}

	function teardownMobileMenu( $sidebar ) {
		if ( $( `#${ mobileMenuId }` ).attr( 'aria-hidden' ) === false ) {
			MicroModal.close( mobileMenuId );
		}

		$sidebar.removeClass( mobileMenuClass );
		$sidebar.removeAttr( 'role' );
		$sidebar.removeAttr( 'aria-modal' );

		$sidebar.unwrap();
		$sidebar.prependTo( $( `#${ originalSidebarContainerId }` ) );

		$( `#${ adminbarId }` ).removeClass( adminbarModifierClass );
	}

	function initMobileMenu() {
		const $sidebar = $( `#${ sidebarId }` );
		const mql = window.matchMedia( `( max-width: ${ breakpoint } )` );

		if ( mql.matches ) {
			setupMobileMenu( $sidebar );
		}

		mql.addListener( ( event ) =>
			event.matches
				? setupMobileMenu( $sidebar )
				: teardownMobileMenu( $sidebar )
		);
	}

	$( document ).ready( function () {
		$( '[data-mobile-menu-toggle]' ).click( function () {
			MicroModal.show( mobileMenuId );
		} );

		initMobileMenu();
	} );
} )( jQuery );
