( function ( $ ) {
	// Set the total height of the sticky header bars in a CSS variable
	// so the P2tenberg sticky header can be offset to the proper Y position.
	function setTopOffset() {
		const adminbarHeight = $( '#wpadminbar' ).height();
		const headerHeight = $( '#masthead' ).height();
		const totalHeight = adminbarHeight + headerHeight;

		document.documentElement.style.setProperty(
			'--editor-header-offset',
			`${ totalHeight }px`
		);
	}

	$( document ).ready( () => {
		setTopOffset();

		$( window ).on( 'resize', $.debounce( 200, setTopOffset ) );
	} );
} )( jQuery );
